export default "import React, { useState } from 'react';\nimport { Button } from 'algae-ui/lib';\n\nexport default () => {\n  const [loading, setLoading] = useState<boolean>(false);\n  const [iconLoading, setIconLoading] = useState<boolean>(false);\n\n  return (\n    <div className=\"button-list\">\n      <div>\n        <Button buttonType=\"primary\" loading>\n          Loading\n        </Button>\n        <Button icon=\"setting\" loading>\n          Loading\n        </Button>\n      </div>\n      <div>\n        <Button\n          buttonType=\"primary\"\n          loading={loading}\n          onClick={() => {\n            setLoading(true);\n          }}\n        >\n          Click me !\n        </Button>\n        <Button\n          icon=\"setting\"\n          loading={iconLoading}\n          onClick={() => {\n            setIconLoading(true);\n          }}\n        >\n          Click me !\n        </Button>\n      </div>\n      <div>\n        <Button buttonType=\"primary\" loading />\n        <Button loading />\n      </div>\n    </div>\n  );\n};\n"