export default "import React from 'react';\nimport { Badge } from 'ROOT/src';\n\nexport default () => {\n  return (\n    <div className=\"badge-list\">\n      <Badge count={5} />\n      <Badge\n        count={0}\n        showZero\n        style={{\n          background: '#FFFFFF',\n          color: '#8F8F8F',\n          boxShadow: '0 0 0 1px #d9d9d9 inset'\n        }}\n      />\n      <Badge count={100} style={{ background: '#56BF14' }} />\n    </div>\n  );\n};\n"