export default "import React from 'react';\nimport { Modal, Button } from 'algae-ui/lib';\n\nexport default () => {\n  const info = () =>\n    Modal.info({\n      title: 'this is title',\n      content: 'some message',\n      onClick: (e) => {\n        console.log('点击了');\n      }\n    });\n\n  const success = () =>\n    Modal.success({\n      title: 'this is title',\n      content: 'some message',\n      onClick: (e) => {\n        console.log('点击了');\n      }\n    });\n\n  const danger = () =>\n    Modal.error({\n      title: 'this is title',\n      content: 'some message',\n      onClick: (e) => {\n        console.log('点击了');\n      }\n    });\n\n  const warning = () =>\n    Modal.warning({\n      title: 'this is title',\n      content: 'some message',\n      onClick: (e) => {\n        console.log('点击了');\n      }\n    });\n\n  return (\n    <div className=\"modal-example-list\">\n      <Button buttonType=\"primary\" onClick={info}>\n        Modal.info\n      </Button>\n      <Button buttonType=\"success\" onClick={success}>\n        Modal.success\n      </Button>\n      <Button buttonType=\"danger\" onClick={danger}>\n        Modal.error\n      </Button>\n      <Button buttonType=\"default\" onClick={warning}>\n        Modal.warning\n      </Button>\n    </div>\n  );\n};\n"