export default "import React from 'react';\nimport { Badge, Avatar } from 'ROOT/src';\n\nexport default () => {\n  return (\n    <div className=\"badge-list\">\n      <Badge count={99}>\n        <Avatar shape=\"square\" />\n      </Badge>\n      <Badge count={100}>\n        <Avatar shape=\"square\" />\n      </Badge>\n      <Badge count={11} overflowCount={10}>\n        <Avatar shape=\"square\" />\n      </Badge>\n    </div>\n  );\n};\n"