export default "import React, { useState } from 'react';\nimport { Checkbox, Button, ButtonGroup } from 'ROOT/src';\n\nexport default () => {\n  const [disabled, setDisabled] = useState<boolean>(false);\n  const [checked, setChecked] = useState<boolean>(false);\n\n  return (\n    <div className=\"checkbox-example-list\">\n      <Checkbox disabled={disabled} checked={checked}>\n        Checkbox Disabled\n      </Checkbox>\n      <br />\n      <br />\n      <ButtonGroup>\n        <Button\n          buttonType=\"primary\"\n          full\n          onClick={() => {\n            setDisabled(!disabled);\n          }}\n        >\n          toggle disabled\n        </Button>\n        <Button\n          buttonType=\"primary\"\n          full\n          onClick={() => {\n            setChecked(!checked);\n          }}\n        >\n          toggle checked\n        </Button>\n      </ButtonGroup>\n    </div>\n  );\n};\n"