export default "import React from 'react';\nimport { Modal, confirm, Button } from 'algae-ui/lib';\n\nexport default () => {\n  const showModal = () =>\n    confirm({\n      title: 'This is a confirm message',\n      content: (\n        <div>\n          <p>some messages...some messages...</p>\n          <p>some messages...some messages...</p>\n        </div>\n      ),\n      iconType: 'question-circle',\n      iconStyle: { fill: 'red' },\n      okButton: {\n        content: 'Ok',\n        onClick: () => {\n          console.log('你点击了 OK');\n        }\n      },\n      cancelButton: {\n        content: 'Cancel',\n        onClick: () => {\n          console.log('你点击了 Cancel');\n        }\n      }\n    });\n\n  const alert = () => {\n    Modal.alert({\n      title: 'This is a Modal.alert message',\n      content: (\n        <div>\n          <p>some messages...some messages...</p>\n          <p>some messages...some messages...</p>\n        </div>\n      ),\n      iconType: 'question-circle',\n      iconStyle: { fill: 'red' },\n      okButton: {\n        content: 'Ok',\n        onClick: () => {\n          console.log('你点击了 OK');\n        }\n      },\n      cancelButton: {\n        content: 'Cancel',\n        onClick: () => {\n          console.log('你点击了 Cancel');\n        }\n      }\n    });\n  };\n\n  return (\n    <div className=\"modal-example-list\">\n      <Button buttonType=\"primary\" onClick={showModal}>\n        confirm\n      </Button>\n      <Button onClick={alert}>Modal.alert</Button>\n    </div>\n  );\n};\n"