export default "import React from 'react';\nimport { Button, message } from 'ROOT/src';\n\nexport default () => {\n  return (\n    <div className=\"message-example-list\">\n      <Button\n        onClick={() => {\n          const destroy = message.loading('这是一条加载中消息', 0);\n          setTimeout(destroy, 3000);\n        }}\n      >\n        loading message\n      </Button>\n    </div>\n  );\n};\n"