export default "import React from 'react';\nimport { Badge, Avatar, Icon } from 'ROOT/src';\n\nexport default () => {\n  return (\n    <div className=\"badge-list\">\n      <Badge count={5} dot>\n        <Avatar shape=\"square\" />\n      </Badge>\n      <Badge dot>\n        <Icon type=\"bell\" />\n      </Badge>\n      <Badge dot style={{ background: '#56BF14' }}>\n        something\n      </Badge>\n    </div>\n  );\n};\n"