export default "import React, { useState } from 'react';\nimport { Rate } from 'ROOT/src';\n\nexport default () => {\n  const [value, setValue] = useState<number>(2);\n  const tips = ['terrible', 'bad', 'normal', 'good', 'wonderful'];\n  return (\n    <div className=\"rate-example-list\">\n      <div className=\"rate-example\">\n        <Rate\n          value={value}\n          allowHalf\n          onChange={(value) => {\n            setValue(value);\n          }}\n          tips={tips}\n        />\n        <span className=\"tips\">{tips[Math.ceil(value) - 1]}</span>\n      </div>\n    </div>\n  );\n};\n"