export default "import React from 'react';\nimport { Button, message, MessageType } from 'ROOT/src';\n\nexport default () => {\n  const types: MessageType[] = ['success', 'error', 'warning'];\n  return (\n    <div className=\"message-example-list\">\n      {types.map((type) => (\n        <Button\n          key={type}\n          onClick={() => {\n            message[type](`这是一条 ${type} 消息`);\n          }}\n        >\n          {type}\n        </Button>\n      ))}\n    </div>\n  );\n};\n"