export default "import React, { useState } from 'react';\nimport { Button, Form, FormValue, Field } from 'ROOT/src';\n\nexport default () => {\n  const [formData, setFormData] = useState<FormValue>({\n    username: '',\n    password: ''\n  });\n\n  const [fields] = useState<Field[]>([\n    {\n      type: 'username',\n      label: '用户名',\n      input: { type: 'text' },\n      rules: [\n        {\n          type: 'custom',\n          match: (value) => {\n            return !!value && value.length % 2 === 0;\n          },\n          messageType: 'warning',\n          message: '长度必须为大于 0 的偶数'\n        }\n      ]\n    },\n    {\n      type: 'password',\n      label: '密码',\n      input: { type: 'password' },\n      rules: [\n        {\n          type: 'required',\n          match: true,\n          messageType: 'warning',\n          message: '不能为空'\n        }\n      ]\n    }\n  ]);\n\n  const onSubmit = () => {\n    console.log('formData', formData);\n  };\n\n  const onChange = (newValue: FormValue) => {\n    setFormData(newValue);\n  };\n\n  return (\n    <div className=\"form-example-list\">\n      <Form\n        value={formData}\n        fields={fields}\n        buttons={\n          <Button full type=\"submit\" buttonType=\"primary\">\n            提交\n          </Button>\n        }\n        onSubmit={onSubmit}\n        onChange={onChange}\n      />\n    </div>\n  );\n};\n"