export default "import React, { useState } from 'react';\nimport { Modal, Button } from 'algae-ui/lib';\n\nexport default () => {\n  const [visible, setVisible] = useState<boolean>(false);\n  const onClose = () => setVisible(false);\n\n  return (\n    <div className=\"modal-list\">\n      <Button buttonType=\"primary\" onClick={() => setVisible(true)}>\n        Open Modal\n      </Button>\n      <Modal\n        visible={visible}\n        onClose={onClose}\n        title=\"Modal Box\"\n        closeOnClickMask\n        buttons={[\n          <Button key=\"btn1\" ghost onClick={onClose}>\n            Cancel\n          </Button>,\n          <Button key=\"btn2\" buttonType=\"success\" onClick={onClose}>\n            OK\n          </Button>\n        ]}\n      >\n        <p>Modal Message</p>\n        <p>Modal Message</p>\n        <p>Modal Message</p>\n      </Modal>\n    </div>\n  );\n};\n"