export default "import React from 'react';\nimport { Badge, Avatar, Icon } from 'ROOT/src';\n\nexport default () => {\n  return (\n    <div className=\"badge-list\">\n      <Badge count={5}>\n        <Avatar shape=\"square\" />\n      </Badge>\n      <Badge count={0} showZero>\n        <Avatar shape=\"square\" />\n      </Badge>\n      <Badge\n        count={\n          <Icon\n            type=\"warning-circle\"\n            style={{ fill: '#e23c39', fontSize: '12px' }}\n          />\n        }\n      >\n        <Avatar shape=\"square\" />\n      </Badge>\n    </div>\n  );\n};\n"