export default "import React, { useState } from 'react';\nimport { Button, Form, FormValue, Field } from 'ROOT/src';\n\nexport default () => {\n  const [formData, setFormData] = useState<FormValue>({\n    username: '',\n    password: ''\n  });\n\n  const existingAccount = ['jack', 'rose'];\n\n  const [fields] = useState<Field[]>([\n    {\n      type: 'username',\n      label: '用户名',\n      input: { type: 'text' },\n      rules: [\n        {\n          type: 'required',\n          match: true,\n          message: '不能为空'\n        },\n        {\n          type: 'minLength',\n          match: 4,\n          message: '不能少于 4 个字'\n        },\n        {\n          type: 'custom',\n          match: (value) =>\n            new Promise((resolve, reject) => {\n              setTimeout(() => {\n                existingAccount.includes(value) ? reject() : resolve();\n              }, 1000);\n            }),\n          message: '该账号已存在'\n        }\n      ]\n    },\n    {\n      type: 'password',\n      label: '密码',\n      input: { type: 'password' },\n      rules: [\n        {\n          type: 'required',\n          match: true,\n          messageType: 'warning',\n          message: '不能为空'\n        },\n        {\n          type: 'minLength',\n          match: 6,\n          messageType: 'warning',\n          message: '不能少于 6 个字'\n        }\n      ]\n    }\n  ]);\n\n  const onSubmit = () => {\n    console.log('formData', formData);\n  };\n\n  const onChange = (newValue: FormValue) => {\n    setFormData(newValue);\n  };\n\n  return (\n    <div className=\"form-example-list\">\n      <Form\n        value={formData}\n        fields={fields}\n        buttons={\n          <Button full type=\"submit\" buttonType=\"primary\">\n            提交\n          </Button>\n        }\n        onSubmit={onSubmit}\n        onChange={onChange}\n      />\n    </div>\n  );\n};\n"