export default "import React, { useState } from 'react';\nimport { RadioGroup } from 'ROOT/src';\n\nexport default () => {\n  const [value1, setValue1] = useState<string>('a');\n  const [value2, setValue2] = useState<string>('a');\n  const [value3, setValue3] = useState<string>('a');\n\n  const options1 = ['a', 'b', 'c'];\n  const options2 = [\n    { label: 'a', value: 'a' },\n    { label: 'b', value: 'b' },\n    { label: 'c', value: 'c' }\n  ];\n  const options3 = [\n    { label: 'a', value: 'a' },\n    { label: 'b', value: 'b' },\n    { label: 'c', value: 'c', disabled: true }\n  ];\n\n  return (\n    <div className=\"radio-example-list\">\n      <RadioGroup\n        options={options1}\n        value={value1}\n        onChange={(e) => {\n          setValue1(e.currentTarget.value);\n          console.log(e.currentTarget.value);\n        }}\n      />\n      <br />\n      <br />\n      <RadioGroup\n        options={options2}\n        value={value2}\n        onChange={(e) => {\n          setValue2(e.currentTarget.value);\n          console.log(e.currentTarget.value);\n        }}\n      />\n      <br />\n      <br />\n      <RadioGroup\n        options={options3}\n        value={value3}\n        onChange={(e) => {\n          setValue3(e.currentTarget.value);\n          console.log(e.currentTarget.value);\n        }}\n      />\n    </div>\n  );\n};\n"