export default "import React from 'react';\nimport { Button, ButtonGroup } from 'algae-ui/lib';\n\nexport default () => {\n  return (\n    <div className=\"button-list\">\n      <div>\n        <ButtonGroup>\n          <Button>Button1</Button>\n          <Button>Button2</Button>\n          <Button>Button3</Button>\n        </ButtonGroup>\n      </div>\n      <div>\n        <ButtonGroup>\n          <Button buttonType=\"primary\" icon=\"left\">\n            Go back\n          </Button>\n          <Button buttonType=\"primary\" icon=\"right\" iconPosition=\"right\">\n            Go forward\n          </Button>\n        </ButtonGroup>\n      </div>\n      <div>\n        <ButtonGroup>\n          <Button buttonType=\"success\" icon=\"cloud\" />\n          <Button buttonType=\"success\" icon=\"setting\" />\n        </ButtonGroup>\n      </div>\n    </div>\n  );\n};\n"