export default "import React, { useState } from 'react';\nimport { Radio, Button } from 'ROOT/src';\n\nexport default () => {\n  const [disabled, setDisabled] = useState<boolean>(true);\n  return (\n    <div className=\"radio-example-list\">\n      <Radio disabled={disabled}>Radio Disabled</Radio>\n      <br />\n      <Radio checked disabled={disabled}>\n        Radio Disabled\n      </Radio>\n      <br />\n      <Button\n        buttonType=\"primary\"\n        onClick={() => {\n          setDisabled(!disabled);\n        }}\n      >\n        Toggle disabled\n      </Button>\n    </div>\n  );\n};\n"