export default "import React, { useState } from 'react';\nimport { CheckboxGroup } from 'ROOT/src';\n\nexport default () => {\n  const [checkedList1, setCheckedList1] = useState<string[]>([]);\n  const [checkedList2, setCheckedList2] = useState<string[]>([]);\n  const [checkedList3, setCheckedList3] = useState<string[]>([]);\n\n  const options = ['A', 'B', 'C'];\n  const options2 = [\n    { label: 'A', value: 'A' },\n    { label: 'b', value: 'B' },\n    { label: 'C', value: 'C' }\n  ];\n  const options3 = [\n    { label: 'A', value: 'A' },\n    { label: 'B', value: 'B' },\n    { label: 'C', value: 'C', disabled: true }\n  ];\n\n  return (\n    <div className=\"checkbox-example-list\">\n      <CheckboxGroup\n        options={options}\n        value={checkedList1}\n        onChange={(value) => {\n          setCheckedList1(value);\n        }}\n      />\n      <br />\n      <CheckboxGroup\n        options={options2}\n        value={checkedList2}\n        onChange={(value) => {\n          setCheckedList2(value);\n        }}\n      />\n      <br />\n      <CheckboxGroup\n        options={options3}\n        value={checkedList3}\n        onChange={(value) => {\n          setCheckedList3(value);\n        }}\n      />\n    </div>\n  );\n};\n"