export default "import React from 'react';\nimport { Progress } from 'ROOT/src';\n\nexport default () => {\n  return (\n    <div className=\"progress-list\">\n      <Progress percent={90} backgroundColor=\"#cccccc\" strokeColor=\"#ffa30a\" />\n      <Progress\n        percent={30}\n        type=\"circle\"\n        backgroundColor=\"#cccccc\"\n        strokeColor=\"#ff721f\"\n      />\n    </div>\n  );\n};\n"