export default "import React, { useState } from 'react';\nimport { Steps, Step, StatusTypes } from 'ROOT/src';\n\nexport default () => {\n  const [current, setCurrent] = useState<number>(1);\n  const [status, setStatus] = useState<StatusTypes>('process');\n\n  return (\n    <div className=\"steps-example-list\">\n      <Steps current={current} status={status}>\n        <Step\n          title=\"success step\"\n          description=\"This is a description\"\n          onClick={() => {\n            setCurrent(0);\n            setStatus('success');\n          }}\n        />\n        <Step\n          title=\"process step\"\n          subTitle=\"This is subTitle\"\n          description=\"This is a description\"\n          onClick={() => {\n            setCurrent(1);\n            setStatus('process');\n          }}\n        />\n        <Step\n          title=\"waiting step\"\n          description=\"This is a description\"\n          onClick={() => {\n            setCurrent(2);\n            setStatus('fail');\n          }}\n        />\n      </Steps>\n    </div>\n  );\n};\n"